.grid-img-container {
  background-color: transparent;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  box-shadow: 6px 10px 10px rgba(27, 27, 27, 0.9);
  border-radius: 25px;
  outline: 0px solid black;
  transition: 0.2s;
}

.grid-img-container:hover{
  box-sizing: border-box;
  border-radius: 25px;
  outline: 8px solid black;
  transition: 0.1s;
}
  
.grid-img-search {
  height: 360px;
  width: 360px;
  max-width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: center center;
  border-radius: 25px;
  transition: all 0.2s ease-in-out;
  z-index: 0;
}

.grid-img-favorites {
  height: 360px;
  width: 360px;
  max-width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: center center;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  transition: all 0.2s ease-in-out;
  z-index: 0;
}

