.icons{
  filter: drop-shadow(7px 7px 7px #000);
  background-color: transparent;
  padding: 8px;
  border-radius: 0%;
  transition: 0.5s;
}

.icons:hover{
  filter: drop-shadow(5px 5px 10px #000);
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 50%;
  transition: 0.5s;
}

.img-data{
  height: 175px;
  padding: 20px;
  padding-top: 8px;
  padding-bottom: 10px;
  text-align: left;
  background: white;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  font-size: 18px;
}

.icon-data{
  position: relative;
  top: 5px;
  left: 0px;
}


@media (max-width: 500px) {
  .grid-img-container {
    margin: 0 auto;
    max-width: 300px;
    max-height:600px;
  }
}